.Comments {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-left: 40px;
}
.Comment {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	margin-top: 15px;
	margin-bottom: 15px;
	line-height: 21px;
	font: normal normal 400 normal 14px "Noto Sans", sans-serif;
	text-align: left;
}
.gilding {
	margin: 0 3px;
}
.gilding img {
	vertical-align: middle;
}
.top-comment {
	width: 780px;
	overflow-y: auto;
	height: 425px;
	background-color: rgb(248, 248, 248);
}
.left-box {
	margin: 3px 0 0 0;
}
.Comment p {
	padding-bottom: 3.5px;
	padding-left: 0;
	padding-right: 0;
	padding-top: 0;
	margin: 0;
}
.Comment * {
	line-height: 21px;
	word-break: break-word;
}
.controls {
	display: flex;
	justify-content: space-between;
	margin: 25px 0;
}
.controls button {
	font-size: 24px;
	padding: 5px 10px;
}
.highlight-danger {
	color: rgb(233, 14, 14);
	text-decoration: underline;
}
.highlight-warn {
	font-weight: 700;
	color: black;
}
.draggable {
	height: 15px;
	margin-bottom: 3px;
	border-radius: 999px;
	background: rgb(225, 231, 234);
}
.draggable.enabled {
	background-color: rgb(124, 211, 141);
}
.draggable.current {
	background-color: rgb(73, 152, 255);
}
.draggable-container {
	height: 800px;
	overflow-y: auto;
	width: 50px;
}