@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,400i,700,700i&display=swap);
.Comments {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-left: 40px;
}
.Comment {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	margin-top: 15px;
	margin-bottom: 15px;
	line-height: 21px;
	font: normal normal 400 normal 14px "Noto Sans", sans-serif;
	text-align: left;
}
.gilding {
	margin: 0 3px;
}
.gilding img {
	vertical-align: middle;
}
.top-comment {
	width: 780px;
	overflow-y: auto;
	height: 425px;
	background-color: rgb(248, 248, 248);
}
.left-box {
	margin: 3px 0 0 0;
}
.Comment p {
	padding-bottom: 3.5px;
	padding-left: 0;
	padding-right: 0;
	padding-top: 0;
	margin: 0;
}
.Comment * {
	line-height: 21px;
	word-break: break-word;
}
.controls {
	display: flex;
	justify-content: space-between;
	margin: 25px 0;
}
.controls button {
	font-size: 24px;
	padding: 5px 10px;
}
.highlight-danger {
	color: rgb(233, 14, 14);
	text-decoration: underline;
}
.highlight-warn {
	font-weight: 700;
	color: black;
}
.draggable {
	height: 15px;
	margin-bottom: 3px;
	border-radius: 999px;
	background: rgb(225, 231, 234);
}
.draggable.enabled {
	background-color: rgb(124, 211, 141);
}
.draggable.current {
	background-color: rgb(73, 152, 255);
}
.draggable-container {
	height: 800px;
	overflow-y: auto;
	width: 50px;
}
/* v-margin = 4 + 18 + 4 + 28 = 54 */
/* acc. h-margin = 35.334 */

.FakeComment {
	text-align: left;
	/* margin: 54px 0 0 35.334px; */
	margin: 54px 0 0 35.334px;
	word-break: break-word;
	line-height: 21px;
	font: normal normal 400 normal 14px "Noto Sans", sans-serif;
}

.FakeComment blockquote {
		border-left: 4px solid rgba(215, 218, 220, 0.2);
		margin: 4px 0px 4px 8px;
		padding-left: 8px;
		quotes: none;
}

.tooTall {
	background: rgba(233, 31, 31, 0.733);
}

.fake-container {
	width: calc(1920px / 2.4 + 10px);
	visibility: hidden;
	position: absolute;
	/* height: calc(1080px / 2.4); */
	margin-top: -50000px;
	overflow: hidden;
}
body {
  margin: 0;
}

.App {
  text-align: center;
  padding: 0;
}

.Thread form div {
  margin-top: 1em;
}
